<template>
<div class="dialog-create">
  <el-dialog :visible.sync="dialogVisible" title="" width="450px">
    <el-form :model="formData" :rules="rules" ref="formCreate" label-width="80px" size="small">
      <el-form-item prop="name" label="">
        <el-input v-model="formData.name" placeholder="" />
      </el-form-item>

      <el-form-item prop="sort" label="">
        <el-input v-model="formData.sort" type="number" min="0" placeholder="" />
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="formData.remark" type="textarea" rows="3" placeholder="" />
      </el-form-item>
    </el-form>

    <span slot="footer">
      <el-button @click="submitForm('formCreate')" type="primary" size="small"></el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'PaymentTypeDialogCreate',
  data () {
    return {
      dialogVisible: false,
      formData: { name: '', sort: '', remark: '' },
      rules: {
        name: [
          { required: true, message: '' },
          { min: 2, max: 20, message: ' 2 - 20 ' }
        ],
        sort: { required: true, message: '' }
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$httpAPI.createTransactionPaymentType(this.formData).then(response => {
            if (response.data.status === 200) {
              this.$utils.initializeObjectProperties(this.formData)
              this.$emit('on-success')
              this.$message.success('')
            } else {
              this.$message.error(`${response.data.msg}`)
            }
          }).catch(error => console.log(error))

          this.dialogVisible = false
        } else {
          this.$message.warning('')
        }
      })
    },
    toggleDialogVisible (status) {
      this.dialogVisible = status
    }
  }
}
</script>
