<template>
<div class="dialog-update">
  <el-dialog :visible.sync="dialogVisible" title="" width="450px">
    <el-form :model="data" label-width="80px" size="small" ref="formUpdate">
      <el-form-item prop="name" label="">
        <el-input :value="data.name" disabled />
      </el-form-item>

      <!-- <el-form-item props="sort" label="">
        <el-select v-model="data.status" placeholder="" style="width: 100%">
          <el-option :value="0" label="" />
          <el-option :value="1" label="" />
        </el-select>
      </el-form-item> -->
      <FormSelectStatic
        @on-change="data.status = $event"
        :options="[
          { value: 0, label: '' },
          { value: 1, label: '' }
        ]"
        label=""
        width="100%"
      />

      <el-form-item props="sort" label="">
        <el-input v-model="data.sort" type="number" min="0" placeholder="" />
      </el-form-item>

      <el-form-item props="remark" label="">
        <el-input v-model="data.remark" type="textarea" rows="3" placeholder="" />
      </el-form-item>
    </el-form>

    <span slot="footer">
      <el-button @click="submitForm" type="primary" size="small"></el-button>
    </span>
  </el-dialog>
</div>
</template>

<script>
export default {
  name: 'PaymentTypeDialogUpdate',
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      dialogVisible: false
    }
  },
  methods: {
    submitForm () {
      this.dialogVisible = false

      const { id, status, sort, remark } = this.data

      this.$httpAPI.updateTransactionPaymentType({ id, status, sort, remark }).then(response => {
        if (response.data.status === 200) {
          this.$emit('on-updated')
          this.$message.success('')
        } else {
          this.$message.error(`${response.data.msg}`)
        }
      }).catch(error => console.log(error))
    },
    toggleDialogVisible (status) {
      this.dialogVisible = status
    }
  }
}
</script>
