<template lang="html">
  <div class="port-create">
    <el-dialog :visible.sync="dialogVisible" title="" width="760px">
      <el-form
        :model="formData"
        :rules="rules"
        label-width="100px"
        size="small"
        ref="formPaymentPortUpdate"
        class="clearfix"
      >
        <el-form-item prop="name" label="">
          <el-input v-model.trim="formData.name" placeholder="" />
        </el-form-item>

        <FormSelect
          @on-change="$set(formData, 'interfaceTypeId', $event)"
          :value="formData.interfaceTypeId"
          httpAPIName="fetchTransactionPortType"
          :httpAPIParams="{ params: { type: 1 } }"
          labelAttr="name"
          valueAttr="dictionaryId"
          prop="interfaceTypeId"
          label=""
          ref="interfaceTypeId"
        />

        <FormSelect
          @on-change="$set(formData, 'payTypeId', $event)"
          :value="formData.payTypeId"
          httpAPIName="fetchTransactionPaymentType"
          :httpAPIParams="{ params: { pageNo: 1, pageSize: 100 } }"
          labelAttr="name"
          valueAttr="id"
          prop="payTypeId"
          label=""
          ref="payTypeId"
        />

        <el-form-item prop="discountRatio" label="%">
          <el-input v-model="formData.discountRatio" type="number" placeholder="" />
        </el-form-item>

        <el-form-item prop="sort" label="">
          <el-input v-model="formData.sort" placeholder="" type="number" />
        </el-form-item>

        <FormRadio
          @on-change="$set(formData, 'status', $event)"
          :value="formData.status"
          :options="[
            { label: 0, title: '' },
            { label: 1, title: '' }
          ]"
          prop="status"
          label=""
          ref="status"
        />

        <FormSelect
          @on-change="$set(formData, 'rebateUserGroups', $event)"
          :value="formData.userGroups"
          httpAPIName="fetchUserGroups"
          :httpAPIParams="{ params: { pageNo: 1, pageSize: 100 } }"
          labelAttr="name"
          valueAttr="groupId"
          prop="rebateUserGroups"
          label=""
          multiple
          ref="rebateUserGroups"
        />

        <el-form-item prop="merchantId" label="" class="custom-block">
          <el-input v-model="formData.merchantId" placeholder="" />
        </el-form-item>

        <el-form-item prop="payAddress" label="" class="custom-block">
          <el-input v-model="formData.payAddress" placeholder="" />
        </el-form-item>

        <el-form-item prop="publicKey" label="()" class="custom-block">
          <el-input v-model="formData.publicKey" placeholder="()" />
        </el-form-item>

        <el-form-item prop="privateKey" label="()" class="custom-block">
          <el-input v-model="formData.privateKey" placeholder="()" />
        </el-form-item>

        <el-form-item prop="ipWhiteList" label="IP" class="custom-block">
          <el-input v-model="formData.ipWhiteList" placeholder="ip" />
        </el-form-item>

        <el-form-item label="" class="custom-block">
          <el-input v-model="formData.remark" type="textarea" rows="3" placeholder="" />
        </el-form-item>
      </el-form>

      <span slot="footer">
        <el-button @click="submitForm('formPaymentPortUpdate')" type="primary" size="small"></el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import FormSelect from '@/components/form/FormSelect'
import FormRadio from '@/components/form/FormRadio'

export default {
  name: 'PaymentPortDialogCreate',
  components: {
    FormSelect,
    FormRadio
  },
  props: {
    data: {
      type: Object,
      required: true
    }
  },
  data () {
    return {
      formData: this.data,
      dialogVisible: false,
      rules: {
        name: { required: true, message: '' },
        interfaceTypeId: { required: true, message: '' },
        payTypeId: { required: true, message: '' },
        discountRatio: { required: true, message: '' },
        status: { required: true, message: '' },
        sort: { required: true, message: '' },
        rebateUserGroups: { type: 'array', required: true, message: '', trigger: 'change' },
        merchantId: { required: true, message: '' },
        payAddress: { required: true, message: '' },
        publicKey: { required: true, message: '()' },
        privateKey: { required: true, message: '()' },
        ipWhiteList: { required: true, message: 'IP' }
      }
    }
  },
  watch: {
    data () {
      this.formData = this.data
    }
  },
  methods: {
    toggleDialogVisible (status) {
      this.dialogVisible = status
    },
    submitForm (formName) {
      this.$refs[formName].validate(valid => {
        this.dialogVisible = false

        if (!valid) {
          this.$httpAPI.createFinanceLimitChange(this.formData).then(response => {
            if (response.data.status === 200) {
              this.$utils.initializeObjectProperties(this.formData)
              this.$utils.invokeRefResetMothod(this.$refs)

              this.$emit('on-created')
              this.$message.success('')
            }
          }).catch(error => console.log(error))
        } else {
          this.$message.warning('')
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.el-form-item {
  float: left;
  width: 50%;
}
</style>
